import React from 'react'

export class ButtonSimbol extends React.Component {
    constructor(props) {
      super(props);
      this.state = {ButtonSimbol: true};

      this.flag = true;
  
      this.handleClick = this.handleClick.bind(this);
    }
  
    handleClick() {
        this.flag = !this.flag;
        this.props.state.stateButtonAbc[this.props.index]=1;
        let simbol = this.props.state.abc[this.props.index];
        let indexSimbol = this.props.state.word.indexOf(simbol);
        if (indexSimbol<0){
          this.props.state.numberPicture++;
          if (this.props.state.numberPicture==5){
            this.props.state.gameState='loss';
          }
        }
        else{
        this.props.state.guessedWord[indexSimbol] = simbol;
        if (!this.props.state.guessedWord.indexOf('')){
          this.props.state.gameState='win';
        }
      }
        this.props.reRender();
      /* this.setState(prevState => ({
        display: !prevState.display
      })); */
    }
  
    render() {
        /* debugger; */
        let c = {backgroundColor: (this.props.state.stateButtonAbc[this.props.index]?'rgba(255,000,000,0.8)':'white')};
      return (
        /* <button onClick={this.handleClick}>
          {this.state.display ? 'a' : 'b'}
        </button> */
        <button onClick={this.handleClick} style={c}>
            {this.props.state.abc[this.props.index]}
        </button>

      );
    }
  }