import React from 'react'
import pictureStyle from '../components/pictureStyle.module.css'
import page1 from '../sranPictures/page1.png'
import page2 from '../sranPictures/page2.png'
import page3 from '../sranPictures/page3.png'
import page4 from '../sranPictures/page4.png'
import page5 from '../sranPictures/page5.png'
import page6 from '../sranPictures/page6.png'
import page7 from '../sranPictures/page7.png'
import page8 from '../sranPictures/page8.png'

export class Picture extends React.Component {
    render() {
     
    const pictures = [page1, page2, page3, page4, page5, page6, page7, page8];

    return(
        
        <div className={pictureStyle.picture}>
        
        <img src={pictures[this.props.numberPicture]} />

        </div>
    )

    }
}