import React from 'react';
import abc from './abc';
import {ButtonSimbol} from './buttonSimbol';
import abcBookStyle from './abcBookStyle.module.css'

export class AbcBook extends React.Component {

    method = () => {
        return abc.map((item, key) => {
            return (
                <div className={abcBookStyle.boxAbc}>
                    <ButtonSimbol reRender={this.props.reRender} state={this.props.state} key={key} index={key} />
                </div>);
        })
    }

    render(){
        return (
            <div className={abcBookStyle.boxConteiner}>
                {this.method()}
            </div>)
    }
}