import React from 'react';
import wordStyle from '../components/wordStyle.module.css'

export class Word extends React.Component {
    /*  */
    method=()=>{
        return this.props.guessedWord.map ((item) => {
           return (
            <div className={wordStyle.boxWord}>
                {item}
            </div>);
    });   
    }

    render() {
    return(
        <div className={wordStyle.word}>
            {this.method()}
        </div>
    )}
}