import React from 'react';
import ReactDOM from 'react-dom';
import { Word } from './components/word';
import { Picture } from './components/picture';
import { AbcBook } from './components/abcBook';
import abc from './components/abc';
import words from './components/words';
//import './App.css';

/* объявить переменную для стейта */

let state = {
  abc:[...abc],
  stateButtonAbc:[],
  word:[],
  guessedWord:[],
  numberPicture:0,
  gameState:'continuation',
}

window.state = state;

let getRandomWord = (min, max) => {
  let rand;
  //rand = min + Math.random() * (max + 1 - min);
  rand = Math.floor(min + Math.random() * (max + 1 - min));
  return rand;
}

function initState(){
  let randomWord = getRandomWord(0, 19);

      let word = words[randomWord];

      for (let i = 0; i<word.length; i++) {
        let simbol = word.charAt(i);
        state.word.push(simbol);
    }

  for (let i=0; i<state.abc.length; i++)
  {
    state.stateButtonAbc.push(0);
  }

  for (let i=0; i<state.word.length; i++)
  {
    state.guessedWord.push("");
  }
}

function App(props) {
  if (state.gameState=='continuation'){

    return (
      <div className="App">
        <Word guessedWord={state.guessedWord} />
        <Picture numberPicture={state.numberPicture}/>
        <AbcBook reRender={props.reRender} state={props.state}/>
      </div>
  );
  }else if(state.gameState=='loss'){
    return (
      <div className="App">
        <Picture numberPicture='6'/>
      </div>
  );
  }else{
    return (
      <div className="App">
        <Picture numberPicture='7'/>
      </div>
  );
  }
}

export default App;

const ourRender =()=>{
ReactDOM.render(<App reRender={reRender} state={state}/>, document.getElementById('root'))};

export const reRender =()=>{ourRender()};

export const initRender =()=>{
  initState();
  ReactDOM.render(<App reRender={reRender} state={state}/>, document.getElementById('root'))};

  